<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left">
                <div class="post__box">
                    <h1>Post information</h1>   
                    <h2>Post title:{{ getPost.title }}</h2>
                    <p>Post body:{{ getPost.body }}</p>
                </div>     
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                posts: '',
                currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
            this.getPosts()
        },
        computed: {
            getPost() {
                return this.posts.find((post) => post.id == this.currentId)
            }
        },
        methods: {
            async getPosts() {
                let res = await fetch('https://jsonplaceholder.typicode.com/posts')
                let infos = await res.json()
                this.posts = infos
            }
        },

    }

</script>
